.practice-menu-container {
    display: block;
    margin-top: 100px;
    height: 79px;
}
.practice-menu-container > h1 {
    font-family: Open Sans;
    font-style: normal;
    font-weight: bold;
    font-size: 34px;
    line-height: 46px;
    text-align: center;
    letter-spacing: 0.14em;
    margin-bottom: 50px;

    color: var(--dark-purple);
}
.practice-menu-container div a {
    display: block;
    width: 381px;
    height: 205px;
    margin: 50px;

    background: #FFFFFF;
    border: 2px solid var(--dark-purple);
    box-sizing: border-box;
    border-radius: 16px;
    text-decoration: none;
    color: var(--dark-purple);
    transition: box-shadow 0.2s, transform 0.2s;
}
.practice-menu-container div a:hover {
    box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.25);
    transform: scale(1.05);
}
.practice-menu-container div a:active {
    transform: scale(1);
}
.practice-menu-container div a h2 {
    font-family: 'Source Code Pro', sans-serif;
    font-style: normal;
    font-weight: normal;
    font-size: 28px;
    line-height: 35px;
    text-align: center;
    margin-top: 30px;
}
.practice-menu-container div a p {
    text-align: center;
    margin: 40px 30px;
    line-height: 1.5;
}
.practice-menu-container div {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
}


.practice-container p {
    text-align: center;
    font-family: 'Open Sans', sans-serif;
    color: var(--dark-purple);
}
.practice-main {
    width: 100vw;
    overflow: hidden;
    height: 100vh;
}
.question {
    color: var(--dark-purple);
    font-family: 'Source Code Pro', sans-serif;
    font-style: normal;
    font-weight: normal;
    font-size: 48px; 
    height: 60px;
    white-space: nowrap;
    transition: transform 0.4s;
}
.question-string {
    color: var(--dark-purple);
    font-family: 'Source Code Pro', sans-serif;
    font-style: normal;
    font-weight: normal;
    font-size: 48px; 
    display: flex;
    height: 60px;
    justify-content: center;
}
.question-string .highlighted {
    background-color: #FAFF00;
    margin-left: 28.8px;
    margin-right: 28.8px;
}
.highlight {
    display: block;
    height: 60px;
    background-color: #FAFF00;
    position: absolute;
    left: 50vw;
    transform: translateX(-50%);
    transition: width 0.4s;
    width: 28.8px;
    border-radius: 5px;
}
.question-string .morse {
    margin: 0 20px;
}
.question-string .sides-left {
    width: calc((100vw - 28.798px)/2 - 20px);
    text-align: right;
    text-overflow: clip;
    overflow: hidden;
    white-space: nowrap;
    direction: rtl;
}
.question-string .sides-right {
    width: calc((100vw - 28.798px)/2 - 20px);
    text-overflow: clip;
    overflow: hidden;
    white-space: nowrap;
}
.question-string .english {
    width: calc((100vw - 28.798px)/2);
}
.practice-container input {
    display: block;
    width: 300px;
    height: 40px;
    font-size: 40px;
    margin: 50px auto;
    transition: background-color 0.1s;
}
.practice-container .green-input {
    background-color: rgb(166, 250, 110);
}
.practice-container .red-input {
    background-color: rgb(250, 110, 110);
}

.back-link {
    display: block;
    margin: 0px 0 50px 50px;
    color: var(--dark-purple);
    text-decoration: none;
    font-family: Open Sans;
    font-size: 20px;
    transition: transform 0.5s;
    width: 400px;
}


.reveal-btn {
    cursor: pointer;
    color: var(--dark-purple);
    display: block;
    text-align: center;
    font-size: 20px;
    padding: 16px;
    width: 150px;
    border-radius: 10px;
    margin: 0 auto;
    transition: transform 0.2s;
    border: 1px solid var(--dark-purple);
    margin-top: 50px;
}
.reveal-btn:active {
    transform: translateY(2px);
}
.reveal-btn:hover {
    transform: scale(1.1);
}
.answer {
    text-align: center;
    font-size: 20px;
    padding: 16px;
    margin-top: 50px;
}
.left-arrow {
    width: 30px;
    transform: translateY(15px);
    margin-right: 20px;
}

.back-link:hover {
    transform: translateX(-20px);

}