nav {
    position: fixed;
    width: 100%;
    height: 104px;
    left: 0px;
    top: 0px;

    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    z-index: 2;
    transition: transform 0.5s, padding-top 0.5s, height 0.5s, box-shadow 0.5s;
    background-color: white;
}

nav ul {
    float: right;
}

nav ul li {
    display: inline-block;
    width: 200px;
    height: 79px;
    margin-top: 25px;
    text-align: center;
}


nav ul li a {
    font-family: Open Sans;
    font-style: normal;
    font-weight: normal;
    font-size: 28px;
    line-height: 46px;
    text-align: center;
    text-decoration: none;
    letter-spacing: 0.035em;
    width: 100%;
    height: 100%;
    display: block;
    transition: transform 0.2s;

    color: var(--dark-purple);
}
nav ul li a:hover {
    transform: scale(1.1);
}
nav ul li a p {
    font-family: 'Open Sans', sans-serif;
    font-style: normal;
    font-weight: 300;
    font-size: 19px;
    line-height: 26px;
    text-align: center;
    margin-top: 11px;
    letter-spacing: 0.085em;
}

nav > a {
    position: absolute;
    width: 420px;
    height: 52px;
    left: 0px;
    top: 15px;
    font-family: Nunito;
    font-style: normal;
    font-weight: 800;
    font-size: 50px;
    line-height: 68px;
    text-align: center;
    text-decoration: none;
    letter-spacing: 0.305em;

    color: var(--dark-purple);
}

.activeNavLink::after {
    content: "";
    display: block;
    width: 139px;
    height: 4px;
    margin-left: auto;
    margin-right: auto;
    margin-top: 29.5px;

    background-color: var(--dark-purple);
    border-radius: 27px 27px 0px 0px;
    transition: display 2s;
}
