/* import google fonts */
@import url('https://fonts.googleapis.com/css2?family=Nunito:wght@400;800&family=Open+Sans:wght@300;400;700&family=Source+Code+Pro&display=swap');

/* colors */
:root {
    --white: #ffffff;
    --dark-purple: #34344A;
}

/* some default stuff */
body {
    font-family: 'Open Sans', sans-serif;
    color: var(--dark-purple);
    height: 100vh;
    background-color: var(--white);
    background-size: 100% auto;
}

.container {
    margin-top: 104px;
    width: 100vw;
    position: absolute;
}

h1 {
    font-family: Open Sans;
    font-style: normal;
    font-weight: bold;
    font-size: 34px;
    line-height: 46px;
    text-align: center;
    letter-spacing: 0.14em;
    margin-bottom: 50px;

    color: var(--dark-purple);
}